<template>
  <div class="homepage-content">
    <SearchBox :allCategories="allCategories" :allJobs="allJobs"></SearchBox>

    <div class="job-listings">
      <div class="container">
        <table class="jobs-table table is-fullwidth">
          <thead>
            <tr>
              <th>Position</th>
              <th>Category</th>
              <th>Location</th>
              <th><!-- space for the caret/chevron icon --></th>
            </tr>
          </thead>
          <tbody>
            <JobListing v-for="job in allJobs" :job="job" v-bind:key="job.id"></JobListing>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
 
<script>
  import axios from 'axios';
  import API from '../app.api.js';
  import SearchBox from '../components/SearchBox';
  import JobListing from '../components/JobListing';
  
  export default {
    components: {
      SearchBox,
      JobListing
    },
    data: function() {
      return {
        allJobs: [],
        allCategories: [],
      }
    },
    mounted(){
      axios
      .get(API.getAllJobsURL())
      .then(response => {
        // console.log('All Jobs:', response.data.jobs); // Debug

        API.allJobListings = JSON.parse(JSON.stringify(response.data.jobs)); // Full copy
        this.allJobs = API.filteredJobListings = response.data.jobs; // Linking objects here
        
        response.data.jobs.forEach((job) => {
          if(this.allCategories.indexOf(job.category) === -1){
            this.allCategories.push(job.category);
          }
        });

        document.querySelector('.Loader').style.display = 'none';
      });
    }
  }
</script>
 
<style scoped>
  .job-listings {
    padding: 25px 15px 40px;
  }

  .job-listings-headings {
    border-bottom: 1px solid #f1f1f1;
  }

  thead th {
    text-transform: uppercase;
    font-size: 0.75rem;
    font-weight: normal;
    color: #9E9E9E;
    padding-top: 20px;
    padding-bottom: 20px;
  }
</style>