<template>
  <div class="listing-page-content content">
    
    <router-link to="/"  class="back-link has-text-info is-flex is-vcentered">
      <span class="icon">
        <i class="fas fa-chevron-left"></i> 
      </span> Back to Search
    </router-link>

    <hr>

    <p>Job #{{ id }}</p>
    <h3 class="job-title">{{ job.title }}</h3>
    <p><i class="job-location">{{ job.location }}</i></p>
    <p class="job-description" v-html="job.description.html"></p>

    <a v-if="job.hostedUrl.length > 0" :href="job.hostedUrl" target="_blank" class="button is-primary">Apply Now</a>
  </div>
</template>
 
<script>
  import axios from 'axios';
  import API from '../app.api.js';

  export default {
    data() {
      return {
        id: this.$route.params.id,
        job: {
          title: '',
          description: Object,
          location: '',
          hostedUrl: ''
        }
      }
    },
    mounted(){
      axios
      .get(API.getAllJobsURL())
      .then(response => {
        // console.log('All Jobs:', response.data.jobs); // Debug
        API.allJobListings = JSON.parse(JSON.stringify(response.data.jobs)); // Full copy
        var jobSet = false;

        API.allJobListings.forEach((job) => {
          if(job.id == this.id) {
            this.job = job;
            jobSet = true;
          }
        });

        if(!jobSet){
          this.job = {
            title: 'No job found',
            description: {
              html: 'There was no job found with the ID specified.'
            },
            location: ''
          }
        }

        document.querySelector('.Loader').style.display = 'none';
      });
    }
  }
</script>
 
<style scoped>
  .back-link {
    cursor: pointer;
  }

  .listing-page-content {
    padding: 35px;
  }
</style>