<template>
  <span class="category-tag tag is-light is-size-7 " v-bind:class="{ active: isActive }" v-on:click="toggleTag(category)">
    # {{ category }}
  </span>
</template>

<script>
  import API from '../app.api'; 

  export default {
    props: {
      category: String
    },
    methods: {
      toggleTag: function(category){
        this.isActive = !this.isActive;

        API.setJobCategoryFilters(category, this.isActive);
      }
    },
    data() {
      return {
        isActive: false
      };
    },

  }
</script>

<style scoped>
  .tag {
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    transition: all 0.15s;
  }

  .tag:hover {
    background: #424242;
    color: #dcdcdc;
  }

  .tag.active {
    background: #424242;
    color: #dcdcdc;
  }

</style>