module.exports = {
  author: 'Shane Hoban',
  authorEmail: 'shanehoban@gmail.com',

  allJobListings: [],
  filteredJobListings: [],
  
  jobCategoryFilters: new Set(), // Sets only contain unique values (have different functions though)
  jobFilterString: '',

  getAllJobsURL: function() {
    return 'https://hirehive-testing-account.hirehive.com/api/v1/jobs'
  },

  // Called on the Job Listing Page
  getJobByID: function(id) {
    return this.allJobListings.filter(function(job) {
      return job.id === id;
    });
  },

  // Called when a user clicks on a category tag
  setJobCategoryFilters: function(category, isActive) {
    // console.log('filtering:', category); // Debug
    // console.log('is active:', isActive); // Debug
    // console.log('jobCategoryFilters:', this.jobCategoryFilters); // Debug

    if(!this.jobCategoryFilters.has(category) && isActive){
      this.jobCategoryFilters.add(category);
    }
    
    else if(this.jobCategoryFilters.has(category) && !isActive){
      this.jobCategoryFilters.delete(category);
    }
    
    else if(this.jobCategoryFilters.has(category) && isActive){
      // theoretically, this should never be hit
    }

    this.applyJobFilters();
  },

  setJobFilterString: function(string) {
    // console.log('setJobFilterString:', string); // Debug
    this.jobFilterString = string.toLowerCase();
    this.applyJobFilters();
  },

  applyJobFilters: function() {
    // Reset the filtered array first (empty it)
    this.filteredJobListings.length = 0;
    // Re-add the listings
    this.allJobListings.forEach((job) => {
      this.filteredJobListings.push(job);
    });

    this.allJobListings.forEach((job) => {
      // * The text search should only work when the user enters 2 characters or more
      if (this.jobFilterString.length > 1) {
        // If the job title, and description don't contain the string, or the category filter is active, and this job doesn't match it => remove the job
        if((job.title.toLowerCase().indexOf(this.jobFilterString) === -1 && job.description.text.toLowerCase().indexOf(this.jobFilterString) === -1) || 
        (this.jobCategoryFilters.size > 0 && !this.jobCategoryFilters.has(job.category))) {
          this.filteredJobListings.splice(this.filteredJobListings.indexOf(job), 1); // Remove said job
        }
      } else {
        // otherwise, if the string isn't set, we just filter by categories
        if(this.jobCategoryFilters.size > 0 && !this.jobCategoryFilters.has(job.category)){
          this.filteredJobListings.splice(this.filteredJobListings.indexOf(job), 1); // Remove said job
        }
      }
    });
        
    // console.log('All job listings:', this.allJobListings); // Debug
    // console.log('Filtered job listings:', this.filteredJobListings); // Debug
  }
}